import React, { useEffect, useState } from "react";
import HighchartsReact from "highcharts-react-official";
import Highcharts from "highcharts/highstock"

const BarChart = (props) => {
    const [options, setOptions] = useState({
        chart:{
            type:'column',
            height:700,
            spacingTop:80,
            spacingRight:40
        },
        title: {
            text: "Number of reviews by rating"
        },
        yAxis:{
            title:{
                text:"#Reviews",
                style:{
                    color:"Black"
                },
            },
            opposite:false
        },
        legend:{
            enabled:true,
            layout:'horizontal',
            align:'center',
            verticalAlign:'top',
        },
        plotOptions:{
            series: {
                stacking: "normal"
            }
        },
        series:[{
            name:"#Reviews",
            data: [],
            tooltip:{
                valueDecimals:2
            }
        }]
    })
    useEffect(() => {
        setOptions((prev) => {
            return{
                ...prev,
                series:[
                    {
                        name:"One Star",
                        data: props.wholeWeek.one,
                        tooltip:{
                            valueDecimals:2
                        }
                    },
                    {
                        name:"Two Star",
                        data: props.wholeWeek.two,
                        tooltip:{
                            valueDecimals:2
                        }
                    },
                    {
                        name:"Three Star",
                        data: props.wholeWeek.three,
                        tooltip:{
                            valueDecimals:2
                        }
                    },
                    {
                        name:"Four Star",
                        data: props.wholeWeek.four,
                        tooltip:{
                            valueDecimals:2
                        }
                    },
                    {
                        name:"Five Star",
                        data: props.wholeWeek.five,
                        tooltip:{
                            valueDecimals:2
                        }
                    }
                ]
            }
        })
    }, [props.wholeWeek])    
    return(
        <>
            {props.wholeWeek ? <HighchartsReact
                highcharts={Highcharts}
                constructorType={'stockChart'}
                options={options}
                allowChartUpdate = {true} />
            : <div>
                loading
            </div>}
        </>
    ) 
}

export default BarChart;