import React, {useEffect, useState} from "react";
import axios from "axios";
import Papa from 'papaparse'
import ColChart from "./colchart";
import BarChart from "./bargraph";
const weekData = {
    data:[],
    one:[],
    two:[],
    three:[],
    four:[],
    five:[],
}

const timeStamp =  (date) => {
    const week = date.split('_');
    var newWeek
    switch(week[1]){
        case "Jan":
            newWeek = new Date(`${week[2]}.01.${week[0]}`)
            return newWeek.getTime();
        case "Feb":
            newWeek = new Date(`${week[2]}.02.${week[0]}`)
            return newWeek.getTime();
        case "Mar":
            newWeek = new Date(`${week[2]}.03.${week[0]}`)
            return newWeek.getTime();
        case "Apr":
            newWeek = new Date(`${week[2]}.04.${week[0]}`)
            return newWeek.getTime();
        case "May":
            newWeek = new Date(`${week[2]}.05.${week[0]}`)
            return newWeek.getTime();
        case "Jun":
            newWeek = new Date(`${week[2]}.06.${week[0]}`)
            return newWeek.getTime();
        case "Jul":
            newWeek = new Date(`${week[2]}.07.${week[0]}`)
            return newWeek.getTime();
        case "Aug":
            newWeek = new Date(`${week[2]}.08.${week[0]}`)
            return newWeek.getTime();
        case "Sep":
            newWeek = new Date(`${week[2]}.09.${week[0]}`)
            return newWeek.getTime();
        case "Oct":
            newWeek = new Date(`${week[2]}.10.${week[0]}`)
            return newWeek.getTime();
        case "Nov":
            newWeek = new Date(`${week[2]}.11.${week[0]}`)
            return newWeek.getTime();
        case "Dec":
            newWeek = new Date(`${week[2]}.12.${week[0]}`)
            return newWeek.getTime();
        default:
            break;
    }
}
const Graphs = () => {
    let [weekD, setWeekD] = useState({});
    useEffect(() => {
        fetch();
        setWeekD(weekData)

    },[])
    
    const fetch = async()=>{
        const res = await axios.get('week_summary_20221228v1.csv')
        const parsed = Papa.parse(res.data, {header:true});
        setcsvData(parsed.data)
        weekData.data.pop()
        weekData.data.reverse()
        weekData.one.pop()
        weekData.one.reverse()
        weekData.two.pop()
        weekData.two.reverse()
        weekData.three.pop()
        weekData.three.reverse()
        weekData.four.pop()
        weekData.four.reverse()
        weekData.five.pop()
        weekData.five.reverse()
    }
    const setcsvData = (csv) => {
        const temp = csv.map((item, index) => {
            weekData.data.push([timeStamp(item['week_of']), parseInt(item['#_reviews'])]);
            weekData.one.push([timeStamp(item['week_of']), parseInt(item['1 star'])]);
            weekData.two.push([timeStamp(item['week_of']), parseInt(item['2 stars'])]);
            weekData.three.push([timeStamp(item['week_of']), parseInt(item['3 stars'])]);
            weekData.four.push([timeStamp(item['week_of']), parseInt(item['4 stars'])]);
            weekData.five.push([timeStamp(item['week_of']), parseInt(item['5 stars'])]);
            return null;
        })
    }
    return(
        <>
            <ColChart data={weekD.data}/>
            <BarChart wholeWeek={weekD}/>
        </>
    )
}
export default Graphs;